.socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px 5px;
  .title {
    margin-bottom: 20px;
    font-size: 20px;
  }
  .wrapperIcons {
    display: flex;
    align-items: center;
    gap: 20px;
    img {
      width: 50px;
      height: 50px;
      background-color: white;
      border-radius: 10px;
    }
  }
}
